import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Select from 'react-select';
import style from '../../static/style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';



function MovieEdit({match}){

    const[movie, setMovie] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            dataType: "jsonp",
            url: `http://127.0.0.1:8000/poster/movies/${id}`
        }).then(response => {
            setMovie(response.data)
        })
    }, [id])

    const[genre, setGenre] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            dataType: "jsonp",
            url: "http://127.0.0.1:8000/poster/genres/"
        }).then(response => {
            setGenre(response.data)
        })
    }, [])

 const[country, setCountry] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            dataType: "jsonp",
            url: "http://127.0.0.1:8000/poster/countries/"
        }).then(response => {
            setCountry(response.data)
        })
    }, [])

const[producer, setProducer] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            dataType: "jsonp",
            url: "http://127.0.0.1:8000/poster/producers/"
        }).then(response => {
            setProducer(response.data)
        })
    }, [])

const[actor, setActor] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            dataType: "jsonp",
            url: "http://127.0.0.1:8000/poster/actors/"
        }).then(response => {
            setActor(response.data)
        })
    }, [])

    const genre_opt = []

    for(let g of genre){
        let option = {value: g.genre_name, label: g.genre_name}
        genre_opt.push(option)
    }

    const actor_opt = []

    for(let a of actor){
        let option = {value: a.actor_name, label: a.actor_name}
        actor_opt.push(option)
    }


    const producer_opt = []

    for(let p of producer){
        let option = {value: p.surname, label: p.surname}
        producer_opt.push(option)
    }


    const country_opt = []

    for(let c of country){
        let option = {value: c.country_name, label: c.country_name}
        country_opt.push(option)
    }

    console.log(movie.genre)


    function Submit(e){
    e.preventDefault();
    const genre = [];
    const country = [];
    const actor = [];
    const producer = [];

    for (let item of e.target.elements.genre.selectedOptions){
       let  genre_dict = {
            'genre_name': item.value
        }
        genre.push(genre_dict);
    }

    for (let item of e.target.elements.country.selectedOptions){
        let country_dict = {
            'country_name': item.value
        }
        country.push(country_dict);
    }

    for (let item of e.target.elements.actor.selectedOptions){
        let actor_dict = {
            'actor_name': item.value
        }
        actor.push(actor_dict);
    }

    for (let item of e.target.elements.producer.selectedOptions){
        let producer_dict = {
            'surname': item.value
        }
        producer.push(producer_dict);
    }


    const data = {
        'movie_name': e.target.elements.movie_name.value,
        'year': e.target.elements.year.value,
        'genre': genre,
        'country': country,
        'producer': producer,
        'actor': actor,
        'plot': e.target.elements.plot.value,

    }

    console.table(data)

        axios({
            method: "PUT",
            data: data,
            headers: {
            'Content-type' : 'application/json'
            },
            url: `http://127.0.0.1:8000/poster/movies/${id}/`
        }).then(response => {
            console.log(response.data)
        })



    window.history.back()

  };

  return (
   <div>
   <div className="choose_bar">
                <div className="container">
                    <Link to={{pathname: `/movies/`, fromDashboard: false }} >Афиша</Link>
                    <Link to={{pathname: `/about/`, fromDashboard: false }}>О нас</Link>
                    <Link to={{pathname: `/contact/`, fromDashboard: false }}>Контакты</Link>
                    <Link href="#">Войти</Link>
                </div>
     </div>

     <div className="container movie_add_page">
        <form onSubmit={Submit}>

         <div className="name_label one_label">
            <label>Название: </label>
               <input type="text" name="movie_name" autocomplete="off" defaultValue={movie.movie_name} required />
         </div>


          <div className="year_label one_label">
            <label>Год: </label>
               <input type="number" name="year" autocomplete="off" defaultValue={movie.year} required />
          </div>

        <div className="genre_label one_label">
          <label>Жанр: </label>
            <Select
                defaultValue = {[]}
                isMulti
                name="genre"
                className="select"
                options={genre_opt}
            />
            <Link to={{pathname: `/add_genre/`, fromDashboard: false }} className="plus_btn"><i class="las la-plus-circle"></i></Link>
         </div>


         <div className="country_label one_label">
            <label>Страна:</label>
            <Select
                defaultValue={[]}
                isMulti
                name="country"
                className="select"
                options={country_opt}
            />
             <Link to={{pathname: `/add_country/`, fromDashboard: false }} className="plus_btn"><i class="las la-plus-circle"></i></Link>
          </div>

         <div class="producer_label one_label">
            <label>Режиссер:</label>
            <Select
                defaultValue={[]}
                isMulti
                name="producer"
                className="select"
                options={producer_opt}
            />
             <Link to={{pathname: `/add_producer/`, fromDashboard: false }} className="plus_btn"><i class="las la-plus-circle"></i></Link>
          </div>

         <div class="actor_label one_label">
             <label>В главных ролях:</label>
            <Select
                defaultValue={[]}
                isMulti
                name="actor"
                className="select"
                options={actor_opt}
            />

              <Link to={{pathname: `/add_actor/`, fromDashboard: false }} className="plus_btn"><i class="las la-plus-circle"></i></Link>
           </div>

                   <div class="plot_label one_label">
                        <label>Описание:</label>
                        <textarea type="text" name="plot" defaultValue={movie.plot} autocomplete="off" required />
                    </div>

                     <div className="form_btn" style={{"text-align": "right"}}>
                        <button type="submit" className="btn btn-primary btn-gradient" value="Submit">Сохранить</button>
                     </div>


          </form>
     </div>

   </div>

  );
}

export default MovieEdit;
